html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}

.pdf-container {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.pdf-container > iframe {
  display: -ms-flexbox;
  display: flex;
  height: unset;
  -ms-flex: 1 1;
      flex: 1 1;
  border-bottom: 1px solid #bbb;
}

.test-controls {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 1;
      flex: 1 1;
  max-height: 100px;
}



